.parent {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.div1 { grid-area: 1 / 4 / 2 / 11; }
.div2 { 
    grid-area: 3 / 1 / 7 / 10; 
}

.sort {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    list-style: none;
    margin: 0;
    padding: 0 40px 0 0;
}

.sort li {
    font-size: 20px;
}
.sort li a {
    color: black;
    text-decoration: none;
    border: 1px solid black;
    border-radius: 30px;
    padding: 10px 20px;
}
.sort li a.active {
    background: grey url('./../../assets/img/concret.jpg') center no-repeat;
    background-size: cover;
    color: #fff;
    font-weight: bold;
    border: none
}

.miniProductCard {
    width: 550px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    
}
.miniProductCard p {
    text-align: center;
    font-size: 20px;
    color: black;
}

.productImg {
    max-width: 550px;
    max-height: 550px;
}

.container {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}

.productLink {
    text-decoration: none;
}