.parent {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.div1 {
    width: 100%;
    height: 100%;
    grid-area: 3 / 1 / 6 / 11;
    position: relative;
}

.contactText  {
    position: absolute;
    top: 0;
    left: 200px;
    width: 400px;
    height: 400px;
    background-color: #fff;
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}
.contactText div {
    display: flex;
    justify-content: center;
    align-items: center;
}
.contactText .div1 { grid-area: 1 / 1 / 2 / 2; }
.contactText .div2 { grid-area: 1 / 2 / 2 / 3; }
.contactText .div3 { grid-area: 2 / 1 / 3 / 2; }
.contactText .div4 { grid-area: 2 / 2 / 3 / 3; }
.contactText .div5 { grid-area: 3 / 1 / 4 / 2; }
.contactText .div6 { grid-area: 3 / 2 / 4 / 3; }
.contactText .div7 { grid-area: 4 / 1 / 5 / 2; }
.contactText .div8 { grid-area: 4 / 2 / 5 / 3; }
.contactText .div9 { grid-area: 5 / 1 / 6 / 2; }
.contactText .div10 { grid-area: 5 / 2 / 6 / 3; }

.contactText a {
    color: black;
    font-weight: bold;
    text-transform: uppercase;
}

.contactText img {
    width: 50px;
}