/* Навигация */

.nav {
  grid-area: 2 / 1 / 8 / 1;
  display: flex;
  align-items: flex-end;
}

.nav ul {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  list-style: none;
  margin: 0;
  padding: 0px 0px 0px 20px;
  width: 100%;
  height: 100%;
}

.nav ul li {
  text-align: left;
  transform: rotate(-90deg);
  transform-origin: left top 0;
  margin-top: 150px;
  transition: 1s;
}

.nav ul li:hover {
  text-decoration: underline;
  font-weight: bold;
}

.nav ul a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}

