.parent {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.div2 {
    grid-area: 1 / 7 / 8 / 9;;
    max-width: 100%;
    overflow: hidden;
}

.div2 div {
    height: 100%;
    background:  url('./../../assets/img/concret.jpg') center no-repeat;
    background-size: cover;
}

.div3 {
    grid-area: 1 / 5 / 8 / 7;
    max-width: 100%;
    overflow: hidden;
    position: relative;
}

.div3 div {
    height: 100%;
    background:  url('./../../assets/img/wood.jpg') center center no-repeat;
    background-size: cover;
}

.div4 {
    grid-area: 2 / 1 / 8 / 5;
    display: grid;
    grid-template-columns: 1fr 16fr;
    grid-template-rows: 1fr 7fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}



@keyframes zoomAndMove {
    0% {
        transform: scale(1);
    }

    20% {
        transform: scale(2);
    }

    40% {
        transform: translateY(150px) scale(2);
    }

    60% {
        transform: translateX(-70px) translateY(150px) scale(2);
    }

    80% {
        transform: translateX(-70px) translateY(-150px) scale(2);
    }

    100% {
        transform: translateX(70px) translateY(-150px) scale(2);
    }
}

.div2 div,
.div3 div {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 20px;
    transition: 4s;
    animation: zoomAndMove 120s linear infinite;
}

.div2 div {
    animation: zoomAndMove 120s linear infinite reverse;
}

.div2 span,
.div3 span {
    text-align: right;
    font-size: 80px;
    font-weight: bold;
    color: #FFF;
    transform: rotate(90deg);
    transform-origin: right top 0;
    position: absolute;
    bottom: 0;
    right: 0;
}

/* Контент главной страницы */

.content {
    grid-area: 2 / 1 / 3 / 3;
    display: flex;
    flex-direction: column;
}
.content .textWrap {
    display: flex;
}
.content h1 {
    margin: 0;
    font-size: 37px;
    text-transform: uppercase;
}
.content h2 {
    font-size: 50px;
    text-align: left;
}

.content .window {
    position: relative;
    width: 455px;
    height: 400px;
    margin-top: 60px;
    overflow: hidden;
}

@keyframes roll {
    0% {
        transform: translateY(0px);
    }

    20% {
        transform: translateY(-81px);
    }

    40% {
        transform: translateY(-166px);
    }

    60% {
        transform: translateY(-251px);
    }

    80% {
        transform: translateY(-336px);
    }

    100% {
        transform: translateY(-421px);
    }
}

.content ul {
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: -10px;
    padding: 0;
    position: absolute;
    top: 0;
    right: 0;
    animation: roll 7s cubic-bezier(0.295, 0.535, 0.000, 1.080) 2s infinite;
}

.content ul li {
    font-size: 39px;
    height: 63px;
    text-align: right;
    padding-right: 8px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    text-transform: uppercase;
}

