.parent {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    padding-bottom: 100px;
}

.div1 {
    grid-area: 1 / 4 / 3 / 9;
    overflow: hidden;
}

.div1 div {
    width: 100%;
    height: 100%;
    background: url('./../../assets/img/logs.jpg') center top no-repeat;
    background-size: cover;
    animation: zoomAndMove 120s linear infinite;
}

@keyframes zoomAndMove {
    0% {
        transform: scale(1);
    }

    20% {
        transform: scale(2);
    }

    40% {
        transform: translateY(100px) scale(2);
    }

    60% {
        transform: translateX(-70px) translateY(100px) scale(2);
    }

    80% {
        transform: translateX(-70px) translateY(-100px) scale(2);
    }

    100% {
        transform: translateX(70px) translateY(-100px) scale(2);
    }
}

.div2 {
    grid-area: 3 / 2 / 6 / 8;
}
.div2 p:last-child {
    padding-bottom: 100px;
}


.div1 {
    position: relative;
}

.div1 span {
    text-align: right;
    font-size: 80px;
    font-weight: bold;
    color: #FFF;
    transform-origin: right top 0;
    position: absolute;
    bottom: 0;
    right: 0;
}

.imageWrap1 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.imageWrap1 .div1 { grid-area: 1 / 1 / 2 / 2; }
.imageWrap1 .div2 { grid-area: 2 / 1 / 3 / 2; }
.imageWrap1 .div3 { grid-area: 1 / 2 / 2 / 3; }
.imageWrap1 .div4 { grid-area: 2 / 2 / 3 / 3; }

.imageWrap1 img {
    width: 400px;
}

.imageWrap2 {
    display: grid;
grid-template-columns: 1fr;
grid-template-rows: repeat(2, 1fr);
grid-column-gap: 0px;
grid-row-gap: 0px;
}

.imageWrap2 .div1 { grid-area: 1 / 1 / 2 / 2; }
.imageWrap2 .div2 { grid-area: 2 / 1 / 3 / 2; }

.imageWrap2 img {
    width: 900px;
}

.contactLink {
    color: black;
}