@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Quicksand', sans-serif;
}

.App {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(7, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  width: 100vw;
  height: 100vh;
}

.content { 
  grid-area: 1 / 2 / 8 / 11; 
  height: 100vh;
}

.empty {
  height: 1px;
}

.nightMode {
  background-color: rgb(32, 32, 32);
  color: rgb(195, 195, 195);
}

.lightMode {
  background-color: #fff;
}

.FirstPage_parent__1QZX4 {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.FirstPage_div2__3QPGD {
    grid-area: 1 / 7 / 8 / 9;;
    max-width: 100%;
    overflow: hidden;
}

.FirstPage_div2__3QPGD div {
    height: 100%;
    background:  url(/wedowood/static/media/concret.d871f89d.jpg) center no-repeat;
    background-size: cover;
}

.FirstPage_div3__34N1i {
    grid-area: 1 / 5 / 8 / 7;
    max-width: 100%;
    overflow: hidden;
    position: relative;
}

.FirstPage_div3__34N1i div {
    height: 100%;
    background:  url(/wedowood/static/media/wood.421e1e39.jpg) center center no-repeat;
    background-size: cover;
}

.FirstPage_div4__3uB_C {
    grid-area: 2 / 1 / 8 / 5;
    display: grid;
    grid-template-columns: 1fr 16fr;
    grid-template-rows: 1fr 7fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}



@keyframes FirstPage_zoomAndMove__6J7mL {
    0% {
        transform: scale(1);
    }

    20% {
        transform: scale(2);
    }

    40% {
        transform: translateY(150px) scale(2);
    }

    60% {
        transform: translateX(-70px) translateY(150px) scale(2);
    }

    80% {
        transform: translateX(-70px) translateY(-150px) scale(2);
    }

    100% {
        transform: translateX(70px) translateY(-150px) scale(2);
    }
}

.FirstPage_div2__3QPGD div,
.FirstPage_div3__34N1i div {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 20px;
    transition: 4s;
    animation: FirstPage_zoomAndMove__6J7mL 120s linear infinite;
}

.FirstPage_div2__3QPGD div {
    animation: FirstPage_zoomAndMove__6J7mL 120s linear infinite reverse;
}

.FirstPage_div2__3QPGD span,
.FirstPage_div3__34N1i span {
    text-align: right;
    font-size: 80px;
    font-weight: bold;
    color: #FFF;
    transform: rotate(90deg);
    transform-origin: right top 0;
    position: absolute;
    bottom: 0;
    right: 0;
}

/* Контент главной страницы */

.FirstPage_content__2OjlW {
    grid-area: 2 / 1 / 3 / 3;
