}

@keyframes FirstPage_roll__VgQdL {
    0% {
        transform: translateY(0px);
    }

    20% {
        transform: translateY(-81px);
    }

    40% {
        transform: translateY(-166px);
    }

    60% {
        transform: translateY(-251px);
    }

    80% {
        transform: translateY(-336px);
    }

    100% {
        transform: translateY(-421px);
    }
}

.FirstPage_content__2OjlW ul {
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: -10px;
    padding: 0;
    position: absolute;
    top: 0;
    right: 0;
    animation: FirstPage_roll__VgQdL 7s cubic-bezier(0.295, 0.535, 0.000, 1.080) 2s infinite;
}

.FirstPage_content__2OjlW ul li {
    font-size: 39px;
