.parent {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(10, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.div1 {
    grid-area: 3 / 1 / 7 / 4;
}
img {
    width: 400px;
}
.div2 {
    grid-area: 3 / 4 / 7 / 7; 
}

.div3 { grid-area: 3 / 1 / 7 / 3; }

.productName {
    font-size: 40px;
    font-weight: bold;
    margin: 0;
}